
ul{
    list-style: none;
    margin: 0;
    padding: 0;
}
h1,h2,h3,h4,h5,h6,p {
    margin-top: 0;
    margin-bottom: 0;
 }
 img {
    display: block;
    max-width: 100%; 
     height: auto; 
 } 

 input{
     display: flex;
     margin-left: auto;
     margin-right: auto;
     padding:10px 20px;
    border-radius: 4px;
    border: 1px solid rgba(47, 48, 58, 0.8);
 }
.gallery{
display: flex;
flex-wrap: wrap;  
justify-content: center;
}
.gallery__list{
    display: flex;
    justify-content: space-between;
    margin: 5px;
    
}
.photo-card{
    display: flex;
    flex-direction: column;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 0px 0px 4px 4px;
    
}
.img__card{
    width: 300px;
    height: 300px; 
    float: left;
    text-align: center;
}
.stats{
    padding: 10px;
    display: flex;
    justify-content: space-between;
}
.stats-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-style: normal;
    color: slategrey;
    font-size: 10px;
}

.button{
    display: block;
    margin-left: auto;
    margin-right: auto;
}
.button.is-hidden{
    display: none;
}
.button > .spinner.is-hidden{
    display: none;
}
